import { useContext, useState } from "react";
import eye from "../../assets/icons/eye.svg";
import Context from "../Context";
import ModalInfo from "../Modal-info/ModalInfo";

const IMC = () => {
  const user = useContext(Context)[1];
  const weight = user.weight.at(-1).weight;
  const size = user.size / 100;

  const imc = (weight / size ** 2).toFixed(2);
  const rangeImc = (imc * 100) / 40;

  const title = "Indice de masse corporelle ?";
  const content = `Qu'est ce que l'IMC. L'indice de masse corporelle (IMC) mesure la corpulence. Il se calcule √† partir de ton poids et de ta taille. Pour calculer l'IMC, il suffit de diviser ton poids (kg) par ta taille (m) au carr√©. Un IMC id√©al serait compris entre 18.5 √† 24.8`;
  const other = (
    <div className='imc'>
      <div className='part-imc'>
        <p>Insuffisance Pond√©rale</p>
      </div>
      <div className='part-imc'>
        <p>Corpulence Normale</p>
      </div>
      <div className='part-imc'>
        <p>Surpoids</p>
      </div>
      <div className='part-imc'>
        <p>Ob√©sit√©</p>
      </div>
      <input onChange={() => null} className='range' type='range' value={rangeImc} />
    </div>
  );

  const [modal, setModal] = useState(false);

  return (
    <section className='info-imc container flex-center' data-testid='info-imc'>
      <h2>
        Mon IMC <img onClick={() => setModal(true)} src={eye} alt='more' />
      </h2>

      <h3>{imc}</h3>
      <p>En fonction de votre taille et de poids</p>

      {other}
      {modal && <ModalInfo title={title} content={content} other={other} setModal={setModal} />}
    </section>
  );
};

export default IMC;
